import FileItem from "jx/editor/editionitems/FileItem";
export default class SoundItem extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "Sound";
        super(Object.assign({
            icoSymbol: "volume-up",
            actions: ["download", "preview"],
            informations: ["asset"],
            backgroundColor: "#ccffd2"
        }, dataObject));
    }
}
